import React, { useState } from 'react';
import EditableTable from '../components/EditableTable';
import { useEscolas } from '../hooks/useEscolas';
import { Download } from 'lucide-react';

const TabelaEditavelTab = ({ setEditingLocation }) => {
  const { 
    escolas, 
    loading: escolasLoading, 
    error: escolasError,
    saveEscola
  } = useEscolas();

  const [tableLoading, setTableLoading] = useState(false);
  const [tableError, setTableError] = useState(null);
  const [updateSuccess, setUpdateSuccess] = useState(false);

  // Funo para exportar dados como CSV
  const exportToCSV = () => {
    const headers = [
      'Escola',
      'Municpio',
      'Endereo',
      'Terra Indgena',
      'Parcerias',
      'Diretoria de Ensino',
      'Ano Criao',
      'Povos',
      'Lnguas',
      'Modalidade',
      'Alunos',
      'Turnos',
      'Estrutura',
      'gua',
      'Coleta Lixo',
      'Internet',
      'Equipamentos',
      'Acesso',
      'Gesto',
      'Funcionrios',
      'Prof. Indgenas',
      'Prof. No Indgenas',
      'Falam Lngua',
      'Formao',
      'Formao Cont.',
      'PPP Prprio',
      'PPP Comunidade',
      'Projetos',
      'Parcerias Univ.',
      'ONGs',
      'Desejos',
      'Redes Sociais',
      'Links Redes',
      'Histria',
      'Latitude',
      'Longitude',
      'Links',
      'Vdeos',
      'Logradouro',
      'Nmero',
      'Complemento',
      'Bairro',
      'CEP',
      'Estado',
      'Nome Professor',
      'Prof. Indgenas (Det.)',
      'Prof. No Indgenas (Det.)',
      'Falam Lngua (Det.)',
      'Formao (Det.)',
      'Visitas Supervisores',
      'Outros Funcionrios (Det.)',
      'Gesto (Det.)',
      'Merenda Diferenciada',
      'Cozinha',
      'Merenda Escolar',
      'Diferenciada'
    ];
    
    const csvContent = [
      headers.join(','),
      ...escolas.map(escola => [
        `"${escola.nome_escola || ''}"`,
        `"${escola.municipio || ''}"`,
        `"${escola.endereco || ''}"`,
        `"${escola.terra_indigena || ''}"`,
        `"${escola.parcerias || ''}"`,
        `"${escola.diretoria_ensino || ''}"`,
        `"${escola.ano_criacao || ''}"`,
        `"${escola.povos || ''}"`,
        `"${escola.linguas || ''}"`,
        `"${escola.modalidade || ''}"`,
        `"${escola.numero_alunos || ''}"`,
        `"${escola.turnos || ''}"`,
        `"${escola.estrutura || ''}"`,
        `"${escola.agua || ''}"`,
        `"${escola.coleta_lixo || ''}"`,
        `"${escola.internet || ''}"`,
        `"${escola.equipamentos || ''}"`,
        `"${escola.acesso || ''}"`,
        `"${escola.gestao || ''}"`,
        `"${escola.funcionarios || ''}"`,
        `"${escola.professores_indigenas || ''}"`,
        `"${escola.professores_nao_indigenas || ''}"`,
        `"${escola.falam_lingua_indigena || ''}"`,
        `"${escola.formacao_professores || ''}"`,
        `"${escola.formacao_continuada || ''}"`,
        `"${escola.ppp_proprio || ''}"`,
        `"${escola.ppp_comunidade || ''}"`,
        `"${escola.projetos || ''}"`,
        `"${escola.parcerias_universidades || ''}"`,
        `"${escola.parcerias_ongs || ''}"`,
        `"${escola.desejos_comunidade || ''}"`,
        `"${escola.redes_sociais || ''}"`,
        `"${escola.links_redes_sociais || ''}"`,
        `"${escola.historia_escola || ''}"`,
        `"${escola.latitude || ''}"`,
        `"${escola.longitude || ''}"`,
        `"${escola.links_uteis || ''}"`,
        `"${escola.link_para_videos || ''}"`,
        `"${escola.logradouro || ''}"`,
        `"${escola.numero || ''}"`,
        `"${escola.complemento || ''}"`,
        `"${escola.bairro || ''}"`,
        `"${escola.cep || ''}"`,
        `"${escola.estado || ''}"`,
        `"${escola.nome_professor || ''}"`,
        `"${escola.professores_indigenas_detalhado || ''}"`,
        `"${escola.professores_nao_indigenas_detalhado || ''}"`,
        `"${escola.falam_lingua_indigena_detalhado || ''}"`,
        `"${escola.formacao_professores_detalhado || ''}"`,
        `"${escola.visitas_supervisores || ''}"`,
        `"${escola.outros_funcionarios_detalhado || ''}"`,
        `"${escola.gestao_detalhado || ''}"`,
        `"${escola.merenda_diferenciada || ''}"`,
        `"${escola.cozinha || ''}"`,
        `"${escola.merenda_escolar || ''}"`,
        `"${escola.merenda_diferenciada_detalhado || ''}"`
      ].join(','))
    ].join('\n');

    const blob = new Blob([csvContent], { type: 'text/csv;charset=utf-8;' });
    const link = document.createElement('a');
    const url = URL.createObjectURL(blob);
    link.setAttribute('href', url);
    link.setAttribute('download', 'escolas_indigenas.csv');
    link.style.visibility = 'hidden';
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };


  // Funo para salvar alteraes na tabela
  const handleTableSave = async (escolaId, updatedData) => {
    try {
      setTableLoading(true);
      setTableError(null);
      
      await saveEscola(escolaId, updatedData);
      
      setUpdateSuccess(true);
      setTimeout(() => setUpdateSuccess(false), 3000);
      
    } catch (error) {
      console.error('Erro ao salvar escola:', error);
      setTableError(error.message);
    } finally {
      setTableLoading(false);
    }
  };

  if (escolasLoading) {
    return (
      <div className="flex items-center justify-center h-64">
        <span className="ml-2 text-gray-400">Carregando escolas...</span>
      </div>
    );
  }

  return (
    <div className="flex flex-col h-full bg-gray-800">
      {/* Cabealho */}
      <div className="flex-shrink-0 p-6 border-b border-gray-700">
        <div className="flex items-center justify-between mb-4">
          <div>
            <h2 className="text-2xl font-bold text-white mb-2">
              Tabela Editvel
            </h2>
            <p className="text-gray-400">
              Edite os dados das escolas diretamente na tabela
            </p>
          </div>
          
          <div className="flex items-center space-x-3">
            {updateSuccess && (
              <div className="text-green-400 text-sm">
                 Dados salvos com sucesso!
              </div>
            )}
            
            <button
              onClick={exportToCSV}
              className="flex items-center space-x-2 px-4 py-2 bg-green-600 hover:bg-green-700 text-white rounded-lg transition-colors"
            >
              <Download size={16} />
              <span>Exportar CSV</span>
            </button>
          </div>
        </div>

      </div>

      {/* Tabela Editvel */}
      <div className="flex-1 overflow-hidden">
        {escolasError ? (
          <div className="bg-red-900 border border-red-700 rounded-lg p-4 m-4">
            <div className="flex items-center space-x-2 text-red-400">
              <span className="text-lg"></span>
              <span>Erro ao carregar escolas: {escolasError}</span>
            </div>
          </div>
        ) : (
          <div className="h-full">
            <EditableTable
              escolas={escolas}
              onSave={handleTableSave}
              loading={tableLoading}
              error={tableError}
            />
          </div>
        )}
      </div>

    </div>
  );
};

export default TabelaEditavelTab;
